.friendMessage {
align-self: flex-start;

}

.friendImageAndText {
    display: flex;

}

.friendImageAndText img {

    width: 48px;
    height: 48px;
    margin-right: 10px;

}

.friendText {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    background-color: rgb(245, 247, 251);
    border-radius: 10px;
    position: relative;
    padding-bottom: 9px;
    padding-top: 7px;
    padding-right: 13px;
    padding-left: 6px;
    box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1),0px 5px 20px 0px rgba(29, 33, 38, 0.03);



}



.friendText:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: -11px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 11px 10px 11px;
    border-color: transparent transparent rgb(245, 247, 251) transparent;
    box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1),0px 5px 20px 0px rgba(29, 33, 38, 0.03);

}

