.message {
align-self: flex-end;
    display: flex;
    flex-direction: column;

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    margin-left: 10px;
    align-self: flex-end;
}

.text {
display: flex;
    flex-direction: column;
    align-items: flex-end;
    background-color: rgb(0, 102, 204);
    border-radius: 10px;
    position: relative;
    padding-bottom: 9px;
    padding-top: 7px;
    padding-right: 6px;
    padding-left: 13px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1),0 5px 20px 0 rgba(29, 33, 38, 0.03);



}

.text:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 11px 10px 11px;
    border-color: transparent transparent rgb(0, 102, 204) transparent;

}

.name {
    color: white;
    margin-bottom: 3px;

}

.messageText {
    color: white;



}

.time {
align-self: flex-end;
    margin-right: 3px;

}